// pages/mapAdvance/select.js
// 
import {Fill, Stroke, Style} from 'ol/style.js';
import {click, platformModifierKeyOnly } from 'ol/events/condition.js';  
import {DragBox, Select} from 'ol/interaction';
import {getWidth} from 'ol/extent.js';

// 
function selectStyle(feature) {
  const fill = new Fill({color: feature.get('COLOR') || '#eeeeee55'})
  const stroke = new Stroke({color: 'rgba(255, 255, 255, 0.75)', width: 2 })
  return new Style({fill, stroke})
}

// 
export function initSelectClick(map, featureSel) {
  const selectClick = new Select({condition: click, style: selectStyle });
  map.addInteraction(selectClick);
  selectClick.on('select', function(e) {featureSel.value = e.selected;});  
  return selectClick;
}

// Ctrl+
export function initSelectBox(map, featureSel) {
  const selectClick = initSelectClick(map, featureSel);
  const selectFeatures= selectClick.getFeatures();
  
  // Ctrl
  const dragBox = new DragBox({condition: platformModifierKeyOnly,});
  map.addInteraction(dragBox);
  
  // 
  dragBox.on('boxstart', function () { 
    selectFeatures.clear();
    featureSel.value = []; 
  }); 
  
  // 
  dragBox.on('boxend', function () {
    let selectedFeatures = [];
    const boxExtent = dragBox.getGeometry().getExtent();
    const worldExtent = map.getView().getProjection().getExtent();
    const worldWidth = getWidth(worldExtent);
    const startWorld = Math.floor((boxExtent[0] - worldExtent[0]) / worldWidth);
    const endWorld = Math.floor((boxExtent[2] - worldExtent[0]) / worldWidth);
    const lyrs = map.getLayers();
    
    for (let world = startWorld; world <= endWorld; ++world) {
      const left = Math.max(boxExtent[0] - world * worldWidth, worldExtent[0]);
      const right = Math.min(boxExtent[2] - world * worldWidth, worldExtent[2]);
      const extent = [left, boxExtent[1], right, boxExtent[3]];      
      
      lyrs.forEach(lyr => { 
        // 
        if (lyr.getSource && lyr.getSource().getFeaturesInExtent) {
          const boxFeatures = lyr.getSource().getFeaturesInExtent(extent)
            .filter((e) => !selectedFeatures.includes(e) && e.getGeometry().intersectsExtent(extent));
          selectedFeatures = [...selectedFeatures, ...boxFeatures];
        }
      })
    }    
    selectFeatures.extend(selectedFeatures);
    featureSel.value = selectedFeatures;
  });
}
